* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul li {
  list-style: none;
}

.country-card__container {
  display: flex;
  align-items: center;
}

.country-card__list {
  padding: 0;
  margin: 0;
  font-size: 30px;
}

.country-list__item {
  display: flex;
  height: 25px;
  align-items: center;
  padding: 0;
}

.country-list__item:not(:last-child) {
  margin-bottom: 40px;
}

.country-list__item--icon {
  width: 70px;
  height: auto;
  margin-right: 5px;
}

.country-list__item--title {
  font-size: 25px;
}

.country-card__title {
  margin: 0;
  font-size: 50px;
}

.card-img {
  height: auto;
  margin-right: 25px;
}

.country-card__item {
  display: flex;
  align-items: center;
}

.country-card__item--title {
  font-size: 40px;
}

.country-card__item--text {
  margin-left: 10px;
}

.country-list {
  list-style: none;
  padding: 0;
}
